import { OrbitControls, PerspectiveCamera } from "@react-three/drei";
import { Canvas } from "@react-three/fiber";
import { FunctionComponent, useEffect, useState } from "react"
import { a, useTrail } from 'react-spring'
import '../../App.scss';

