interface Desctription {
  main: string,
  sub: string
}

interface Props {
  activeScene: number
}

const WebDev: FunctionComponent<Props> = (props) => {
  
  const [open, set] = useState(false) 
  const items = [0, 1, 2, 3, 4]
  const trail = useTrail(items.length, {
    config: { mass: 4, tension: 1400, friction: 200 },
    opacity: open ? 1 : 0,
    x: open ? 0 : 80,
    minus_x: open ? 0 : -80,
    angle: open ? 0 : 100
  })

  const column = (title: string, icon: string, texts: Desctription[]) => {

    return(
      <div className="three-containers">
        <a.div className="icon-container" style={{ x: trail[3].x, opacity: trail[3].opacity, rotateZ: trail[3].angle }}>
          <img
            src={process.env.PUBLIC_URL + '/' + icon}
            style={{
              height: "50%",
              color: "white",
              zIndex: 20
            }}
          />
        </a.div>
        <a.div className="column-title" style={{ opacity: trail[1].opacity, x: trail[1].x }}>{title}</a.div>
        {texts.map(({ main, sub }, i) => (
          <div className="about-list-item">
            <a.div className="about-text justify" style={{ opacity: trail[i+1].opacity, x: trail[i+1].x }}>{main}</a.div>
            <a.div className="about-text-small justify" style={{ opacity: trail[i+1].opacity, x: trail[i+1].x }}>{sub}</a.div>
          </div>
        ))}
      </div>
    )
  }

  useEffect(() => {
    set(props.activeScene === 1)
  }, [props.activeScene])

  return(
    <div className="subject-canvas web-dev">
      <div style={{ position: "absolute", width: "100%", height: "100%" }}>
        <Canvas className="canvas">
          <ambientLight intensity={1}  />
          <mesh rotation={[1, 1, 1]}>
            <boxGeometry />
            <meshPhongMaterial />
          </mesh>
          <PerspectiveCamera makeDefault position={[0, 5, 5]}>
            <pointLight position={[5, 5, 0]} color="green" intensity={4} />
            <pointLight position={[-5, 5, 0]} color="purple" intensity={4} />
          </PerspectiveCamera>
          <OrbitControls autoRotate />
        </Canvas>
      </div>
      <div className="container-wd">
        {column("React", "react-icon.svg", desctriptions1)}
        {column("Three.js", "threejs-icon.svg", desctriptions2)}
        {column("Work Experience", "react-icon.svg", desctriptions3)}
      </div>
    </div>
  )
}

export { WebDev }