import { useState } from 'react'
import { SceneButton } from './SceneButton'
import '../App.scss';
import { WhoAmI, WebDev, GameDev } from './scenes'

const Home = () => {
  const [activeScene, setActiveScene] = useState(0)

  const scenesText = ["Who Am I", "Web Development", "Game Development"]
  const scenesId = [0, 1, 2]
  
  const handleChangeScene = (id: number) => {
    setActiveScene(id)
  }

  return (
    <div className="app">
      <div className="canvas-group translate" sceneId={activeScene} >
        <WhoAmI activeScene={activeScene} />
        <WebDev activeScene={activeScene} />
        <GameDev activeScene={activeScene} />
      </div>
      <div className='footer'>
        <div className='button-container'>
          {scenesId.map((id) => <SceneButton text={scenesText[id]} onclick={() => handleChangeScene(id)} />)}
        </div>
      </div>
    </div>
  )
}

export { Home }